.aside__content {
  min-height: 289px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 20px;
    width: 280px;
    min-height: 289px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    min-height: 289px;
    background-image: url("../img/jpeg/aside-bacground.jpg");
    background-repeat: no-repeat;
    background-position: 20px;
    z-index: -2;
  }
}

.aside__wrapper {
  padding: 0 20px;
  padding-top: 58px;
  text-align: center;
}

.aside__text {
  font-size: 16px;
  line-height: 20px;
  color: $color-white;
}

.aside__text-inner {
  padding: 0 60px;
  padding-top: 61px;
}
